import BrowserWindow from "sketch-module-web-view";
const RPCError = require("./errors");
const { MethodNotFound } = require("./errors");
let sendRaw = () => { };
let pending = typeof NSThread !== 'undefined' ?
    NSThread.mainThread().threadDictionary() :
    {}; //  
let methods = {};
function sendJson(req, identifier) {
    try {
        sendRaw(req, identifier);
    }
    catch (err) {
        console.error(err);
    }
}
function sendResult(id, result, identifier) {
    sendJson({
        jsonrpc: "2.0",
        id,
        result
    }, identifier);
}
function sendError(id, error, identifier) {
    const errorObject = {
        code: error.code,
        message: error.message,
        data: error.data
    };
    sendJson({
        jsonrpc: "2.0",
        id,
        error: errorObject
    }, identifier);
}
function handleRaw(data, identifier) {
    try {
        if (!data) {
            return;
        }
        handleRpc(data, identifier);
    }
    catch (err) {
        console.error(err);
        console.error(data);
    }
}
function handleRpc(json, identifier) {
    if (typeof json.id !== "undefined") {
        if (typeof json.result !== "undefined" ||
            json.error ||
            typeof json.method === "undefined") {
            pending[json.id] = json;
        }
        else {
            handleRequest(json, identifier);
        }
    }
    else {
        handleNotification(json, identifier);
    }
}
function onRequest(method, params, identifier) {
    const _method = methods[method] || methods[identifier + '.' + method];
    if (!_method) {
        throw new MethodNotFound(method);
    }
    return _method(...params);
}
function handleNotification(json, identifier) {
    if (!json.method) {
        return;
    }
    onRequest(json.method, json.params, identifier);
}
function handleRequest(json, identifier) {
    if (!json.method) {
        sendError(json.id, new RPCError.InvalidRequest("Missing method"), identifier);
        return;
    }
    try {
        const result = onRequest(json.method, json.params, identifier);
        if (result && typeof result.then === "function") {
            result
                .then((res) => sendResult(json.id, res, identifier))
                .catch((err) => {
                sendError(json.id, err, identifier);
            });
        }
        else {
            sendResult(json.id, result, identifier);
        }
    }
    catch (err) {
        sendError(json.id, err, identifier);
    }
}
export function setup(_methods, IDENTIFIER = 'prism.webview') {
    const handlerName = '_prism';
    if (typeof NSThread !== "undefined") {
        const { getWebview } = require('sketch-module-web-view/remote');
        let webview = getWebview(IDENTIFIER);
        if (!webview) {
            const options = {
                identifier: IDENTIFIER,
                width: 240,
                height: 180,
                show: false,
                webPreferences: {
                    devTools: true,
                }
            };
            webview = new BrowserWindow(options);
            webview.webContents.on(handlerName, (message) => {
                handleRaw(message, IDENTIFIER);
            });
        }
        sendRaw = (message, identifier) => {
            const _webview = getWebview(identifier);
            message.id = message.id + '';
            const evalValue = 'window.' + handlerName + '(\'' + JSON.stringify(message) + '\')';
            _webview.webContents.executeJavaScript(evalValue);
        };
        _methods = Object.keys(_methods).reduce((acc, key) => {
            acc[IDENTIFIER + '.' + key] = _methods[key];
            return acc;
        }, {});
    }
    else if (typeof window !== "undefined") {
        window[handlerName] = (message) => {
            handleRaw(JSON.parse(message), IDENTIFIER);
        };
        sendRaw = (message) => {
            window.postMessage(handlerName, message);
        };
    }
    Object.assign(methods, _methods);
}
const sendNotification = (method, params, identifier) => {
    sendJson({ jsonrpc: "2.0", method, params }, identifier);
};
const isSketch = () => (typeof NSUUID !== 'undefined');
export function sendRequest(method, params, timeout, identifier) {
    console.log('sendRequest', method, params, timeout);
    return new Promise((resolve, reject) => {
        const id = 'req.' + (isSketch() ? NSUUID.UUID().UUIDString() : Date.now());
        const req = { jsonrpc: "2.0", method, params, id };
        const fiber = isSketch() ? require('sketch/async').createFiber() : { cleanup: () => { } };
        const callback = (err, result) => {
            if (err) {
                const jsError = new Error(err.message);
                jsError.code = err.code;
                jsError.data = err.data;
                reject(jsError);
                return;
            }
            resolve(result);
        };
        let receiver;
        let start = Date.now();
        receiver = () => {
            const result = pending[id];
            const isTimeout = (Date.now() - start) >= 1000;
            if (result) {
                delete pending[id];
                //    
                callback(result.error, result.result);
                fiber.cleanup();
            }
            else if (isTimeout) {
                //     
                fiber.cleanup();
            }
            else {
                setTimeout(receiver, 5);
            }
        };
        setTimeout(receiver, 1);
        sendJson(req, identifier);
    });
}
;
